.landing
{
    height: 90vh;
    width:100vw;
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-items: left;
    z-index: 3;
}

.addPost
{
    margin-top: 15px;
    margin-left: 35%;
    margin-right: 35%;
    background-color: rgb(148, 159, 255);
    text-align: center;
    font-size: 32;
    border-radius: 30px;
}

.scrollview
{
    overflow-y: scroll;
    padding-bottom: 200px;
}

::-webkit-scrollbar {
    width: 1em;
}
 
::-webkit-scrollbar-track {
    opacity: 0;
}
 
::-webkit-scrollbar-thumb {
  padding-top: 35%;
  padding-bottom: 35%;
  background-color: rgba(48, 48, 48, 0.1);
  outline: 1px solid rgba(48, 48, 48, 0.1);
  filter: blur(2px);
  border-radius: 5px;
}