@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes drop {
  0% {
    bottom: 0px;
    opacity: 1;
  }
  
  80% {
    opacity: 1;
  }
  
  100% {
    opacity: 1;
    bottom: -90vh;
  }
}

body
{
  overflow: hidden;
}
.BackgroundGradient
{
  z-index: 0;
  height: 100vh;
  width: 100vw;
  background: linear-gradient(to bottom, white, grey, black);
}
.Heading
{
  display: flex;
  flex-direction: column;

  justify-content: center;
  align-items: center;

  background-color: rgb(255, 0, 128);
  border-radius: 15px;

  margin-top: 2%;
  margin-left: 25%;
  margin-right: 25%;

  z-index: 1;
}

