.Wrapper
{
    display: block;
    border: 5px solid rgba(85, 221, 255, 0.1);
    background-color: rgba(85, 221, 255, 0.1);
    filter: brightness(4px);
    margin-top: 20px;
    margin-left: 5px;
    margin-right: 15%;

    border-radius: 5px;

    z-index: 3;

    animation-name: fade-in-slide;
    animation-duration: 5s;
    animation-fill-mode: forwards;
}
.ProfileInfo
{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;

    margin-left: 15px;

    align-items:left;
    justify-content: flex-start;
}
.SongInfo
{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;




    justify-content: right;
    align-items: flex-end;
    flex-grow: 1;

    padding-right: 20px;
    padding-bottom: 20px;
}
.name
{
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;

    font-weight: bold;

    color: black;

}
.preferedGenre
{
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    color: rgb(110, 110, 110);
}

.Genre
{
    color: rgb(255, 255, 255);
}

.Song
{
    font-weight: bolder;
    color: blueviolet;
}

.SelectedSong
{
    color: rgb(47, 7, 83);
}

@keyframes fade-in-slide
{
    0%
    {
        opacity: 0;
        transform: translateX(-500);
    }
    100%
    {
        opacity: 100;
        transform: translateX(0);;
    }
}

