@keyframes popup {
    0%
    {
        opacity: 0%;
        box-shadow: 50px 50px 4px black;
        margin-left: 30%;
        margin-top: 3%;
    }

    100%
    {
        opacity: 100%;
        box-shadow: 8px 8px 4px black;
        margin-left: 35%;
        margin-top: 5%;
    }
}

@keyframes fadein {
    0%
    {
        opacity: 0%;
    }

    100%
    {
        opacity: 30%;
    }
}
.Backdrop
{
    height:  100vw;
    width: 100vw;
    top: 0;
    left: 0;
    position:absolute;
    background: linear-gradient(to bottom, rgb(255, 136, 0), rgb(238, 255, 0), white);
    opacity: 30%;


    display: flex;
    align-items: center;
    justify-content: center;

    z-index: 8;

    animation: fadein 2s ease-in both;
}
.xButton
{
    align-items: center;
    justify-content: center;
    text-align: center;
}
.ForumSection
{
    margin-top: 20px;
    margin-left: 5px;
}
#Container
{

    animation: popup 3.5s ease-in-out forwards;


    position: absolute;
    transform-origin: center;

    top: 0;
    left:0;

    margin-left: 35%;
    margin-top: 5%;

    height: 60vh;
    width: 30vw;
    background-color: white;
    
    display: block;

    opacity: 100;

    z-index: 10;

    border-radius: 15px;

    box-shadow: 8px 8px 4px black;
}

.ForumContent
{
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.SendContent
{
    display: flex;
    flex-direction: row;

    justify-content: center;
    align-items: center;

    margin-top: 60px;


}

.btn
{
    justify-content: space-around;
    font-weight: bold;
    font-size: 40px;
    block-size: 72px;
    border-radius: 20px;
    margin: 60px;
}